
import { createRouter, createWebHashHistory } from 'vue-router';
export default ({ store }) => {
	let routes = [
		
		{
			path: '/', 
			alias: '/home',
			name: 'home' , 
			component: () => import('./pages/home/home.vue'),
			props: true
		},
		//Dashboard routes


//clientes routes
			{
				path: '/clientes/:index?/:fieldName?/:fieldValue?',
				name: 'clienteslist',
				component: () => import('./pages/clientes/list.vue'), 
				props: true
			},
			
	
			{ 
				path: '/clientes/view/:id', 
				name: 'clientesview', 
				component: () => import('./pages/clientes/view.vue'), 
				props: true
			},
	
			{ 
				path: '/clientes/add', 
				name: 'clientesadd', 
				component: () => import('./pages/clientes/add.vue'), 
				props: true
			},
	
			{ 
				path: '/clientes/edit/:id', 
				name: 'clientesedit', 
				component: () => import('./pages/clientes/edit.vue'), 
				props: true
			},
		

//datosprueba routes
			{
				path: '/datosprueba/:index?/:fieldName?/:fieldValue?',
				name: 'datospruebalist',
				component: () => import('./pages/datosprueba/list.vue'), 
				props: true
			},
			
	
			{ 
				path: '/datosprueba/view/:id', 
				name: 'datospruebaview', 
				component: () => import('./pages/datosprueba/view.vue'), 
				props: true
			},
	
			{ 
				path: '/datosprueba/add', 
				name: 'datospruebaadd', 
				component: () => import('./pages/datosprueba/add.vue'), 
				props: true
			},
	
			{ 
				path: '/datosprueba/edit/:id', 
				name: 'datospruebaedit', 
				component: () => import('./pages/datosprueba/edit.vue'), 
				props: true
			},
		

//historico routes
			{
				path: '/historico/:index?/:fieldName?/:fieldValue?',
				name: 'historicolist',
				component: () => import('./pages/historico/list.vue'), 
				props: true
			},
			
	
			{ 
				path: '/historico/view/:id', 
				name: 'historicoview', 
				component: () => import('./pages/historico/view.vue'), 
				props: true
			},
	
			{ 
				path: '/historico/add', 
				name: 'historicoadd', 
				component: () => import('./pages/historico/add.vue'), 
				props: true
			},
	
			{ 
				path: '/historico/edit/:id', 
				name: 'historicoedit', 
				component: () => import('./pages/historico/edit.vue'), 
				props: true
			},
		

//pedido routes
			{
				path: '/pedido/:index?/:fieldName?/:fieldValue?',
				name: 'pedidolist',
				component: () => import('./pages/pedido/list.vue'), 
				props: true
			},
			
	
			{ 
				path: '/pedido/view/:id', 
				name: 'pedidoview', 
				component: () => import('./pages/pedido/view.vue'), 
				props: true
			},
	
			{ 
				path: '/pedido/add', 
				name: 'pedidoadd', 
				component: () => import('./pages/pedido/add.vue'), 
				props: true
			},
	
			{ 
				path: '/pedido/edit/:id', 
				name: 'pedidoedit', 
				component: () => import('./pages/pedido/edit.vue'), 
				props: true
			},
		

//produtos routes
			{
				path: '/produtos/:index?/:fieldName?/:fieldValue?',
				name: 'produtoslist',
				component: () => import('./pages/produtos/list.vue'), 
				props: true
			},
			
	
			{ 
				path: '/produtos/view/:id', 
				name: 'produtosview', 
				component: () => import('./pages/produtos/view.vue'), 
				props: true
			},
	
			{ 
				path: '/produtos/add', 
				name: 'produtosadd', 
				component: () => import('./pages/produtos/add.vue'), 
				props: true
			},
	
			{ 
				path: '/produtos/edit/:id', 
				name: 'produtosedit', 
				component: () => import('./pages/produtos/edit.vue'), 
				props: true
			},
		

//registro routes
			{
				path: '/registro/:index?/:fieldName?/:fieldValue?',
				name: 'registrolist',
				component: () => import('./pages/registro/list.vue'), 
				props: true
			},
			
	
			{ 
				path: '/registro/view/:id', 
				name: 'registroview', 
				component: () => import('./pages/registro/view.vue'), 
				props: true
			},
	
			{ 
				path: '/registro/add', 
				name: 'registroadd', 
				component: () => import('./pages/registro/add.vue'), 
				props: true
			},
	
			{ 
				path: '/registro/edit/:id', 
				name: 'registroedit', 
				component: () => import('./pages/registro/edit.vue'), 
				props: true
			},
		

		
		
		
		
		{ 
			path:  '/error/forbidden', 
			name: 'forbidden', 
			component: () => import('./pages/errors/forbidden.vue'),
			props: true
		},
		{ 
			path: '/error/notfound', 
			name: 'notfound',
			component: () => import('./pages/errors/pagenotfound.vue'),
			props: true
		},
		{
			path: '/:catchAll(.*)', 
			component: () => import('./pages/errors/pagenotfound.vue')
		}
	];
	

	const router = createRouter({
		history: createWebHashHistory(),
		routes,
		scrollBehavior(to, from, savedPostion){
			//if(savedPostion) return savedPostion;
			return { x:0, y:0 }
		}
	});

	
	return router;
}
